<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="loadData" pulling-text="下拉刷新" loosing-text="松开我">
      <h3>京喜</h3>
      <div style="height: 1200px;"></div>
    </van-pull-refresh>
    
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {

    name: 'jingxi',

    data() {
      return {
        isLoading: false
      };
    },

    methods: {
      loadData() {

        let self = this;
        //发起请求
        console.log('发起请求');
        $.ajax({
          type: 'GET',
          url: 'http://www.arthurdon.top:3000/playlist/detail',
          data: {
            id: 24381616
          },
          success: function (data) {
            console.log('data ==> ', data);
            console.log('请求成功');
            self.isLoading = false;
          }
        })
      }
    }
    
  }
</script>>